import React from 'react';
import NVD3Chart from 'react-nvd3';

const datum = [
  { key: 'Carrots', y: 29, color: 'orange' },
  { key: 'Beans', y: 100, color: 'green' },
  { key: 'Melon', y: 32, color: 'yellow' },
  { key: 'Cherries', y: 196, color: 'purple' },
  { key: 'Tomato', y: 200, color: 'red' }
];

const PieDonutChart = () => {
  return <NVD3Chart id="chart" height={300} type="pieChart" datum={datum} x="key" y="y" donut labelType="percent" />;
};

export default PieDonutChart;
