import React from 'react';
import NVD3Chart from 'react-nvd3';

function generateNumber(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min);
}

function getDatum() {
  let sin = [],
    sin2 = [],
    sin3 = [],
    sin4 = [],
    sin5 = [],
    sin6 = [],
    sin7 = [],
    sin8 = [];

  const len = 12 ;
  for (let i = 0; i < len; i++) {
    sin.push({
      x: i,
      y: generateNumber(500, 10000)
    });
    sin2.push({
      x: i,
      y: generateNumber(500, 10000)
    });
    sin3.push({
      x: i,
      y: generateNumber(500, 10000)
    });
    sin4.push({
      x: i,
      y: generateNumber(500, 10000)
    });
    sin5.push({
      x: i,
      y: generateNumber(500, 10000)
    });
    sin6.push({
      x: i,
      y: generateNumber(500, 10000)
    });
    sin7.push({
      x: i,
      y: generateNumber(500, 10000)
    });
    sin8.push({
      x: i,
      y: generateNumber(500, 10000)
    });
  }
  return [
    {
      values: sin,
      key: 'Sales',
      color: '#A389D4'
    },
    {
      values: sin3,
      key: 'Man Power',
      color: '#04a9f5'
    },
    {
      values: sin4,
      key: 'Fertalizers',
      color: '#1de9b6',
      area: true
    },
    {
      values: sin5,
      key: 'pestesides',
      color: '#1dacb6',
      area: true
    },
    {
      values: sin6,
      key: 'overheads',
      color: '#9Ae9b6',
      area: true
    },
    {
      values: sin7,
      key: 'profit',
      color: '#29e7e6',
      area: true
    },
    // {
    //   values: sin8,
    //   key: 'Cash Flow',
    //   color: 'yellow',
    //   area: true
    // },
   
  ];
}

const MultiBarChart = () => {
  const data = getDatum();
  return <NVD3Chart type="multiBarChart" stacked datum={data} x="week" y="$" height={300} showValues groupSpacing={0.2} />;
};

export default MultiBarChart;
